@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&family=Russo+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;900&family=Press+Start+2P&family=Russo+One&display=swap');

*{
  padding: 0;
  margin: 0;
}
.App{
  display: grid;
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 50px 1fr;
  grid-template-areas: 
  "header header"
  "nav content";
  width: 80%;
  margin: 0 auto;

  column-gap: 12px;
  row-gap: 15px;
 
  
}
html{
  background-color: rgb(141, 141, 141);
}

a{
  text-decoration: none;
}



