.countPages {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr;

}
.active {
    font-size: 16px;
    font-weight: 900;
    color: rgb(3, 0, 51);
    cursor: pointer;
    text-align: center;
    border: 2px solid white;
    border-radius: 12px;
    padding: 6px;
    margin: 6px;
    transition-duration: 0.5s;
    background-color: rgba(187, 191, 255, 0.466);


}

.link {
    font-size: 16px;
    font-weight: 900;
    color: rgb(3, 0, 51);
    cursor: pointer;
    text-align: center;
    border: 2px solid white;
    border-radius: 12px;
    padding: 6px;
    margin: 6px;
    transition-duration: 0.5s;
    background-color: rgb(187, 191, 255);

}
.link:hover {
    font-size: 16px;
    font-weight: 900;
    color: rgb(0, 0, 0);
    cursor: pointer;
    text-align: center;
    border: 2px solid white;
    border-radius: 12px;
    padding: 6px;
    margin: 6px;
    transition-duration: 0.5s;
    background-color: rgb(92, 102, 255);

}
